@import url(https://fonts.googleapis.com/css?family=Bree+Serif&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    margin: 0;
    padding: 0;
    text-decoration: none;
    font-family: monospace;
    box-sizing: border-box;
}

body {
    min-height: 100vh;
    background: -webkit-linear-gradient(330deg,#273862,#ab7ef3);
    background: linear-gradient(120deg,#273862,#ab7ef3);
}

.login-form {
    width: 360px;
    background: -webkit-linear-gradient(330deg,#ff6767,rgb(53, 65, 246));
    background: linear-gradient(120deg,#ff6767,rgb(53, 65, 246));
    box-shadow: 0px 0px 10px rgb(0, 0, 0);
    padding: 80px 40px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.login-form h1 {
    text-align: center;
    margin-bottom: 60px;
}

.txtb {
    border-bottom: 2px solid #cfe4ff;
    position: relative;
    margin: 30px 0;
    /* border: 1px solid blue */
}

.txtb input {
    font-size: 15px;
    color: #333;
    border: none;
    width: 100%;
    outline: none;
    background: none;
    padding: 0 5px;
    height: 40px;
}

.txtb span::before {
    content: attr(data-placeholder);
    position: absolute;
    top: 50%;
    left: 5px;
    color: #d1d1d1;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    z-index: -1;
    -webkit-transition: .5s;
    transition: .5s;
}

.focus + span::before {
    top: -5px;
}

.logbtn {
    display: block;
    width: 100%;
    height: 50px;
    border: none;
    background: -webkit-linear-gradient(330deg,#8e77ff, #ff3b3b);
    background: linear-gradient(120deg,#8e77ff, #ff3b3b);
    background-size: 200%;
    outline: none;
    color: white;
    font-size: 1.2rem;
    cursor: pointer;
    -webkit-transition: .5s;
    transition: .5s;
}

.logbtn:hover {
    background-position: right;
}

.bottom-text {
    margin-top: 60px;
    text-align: center;
    font-size: 13px;
}
.friends-list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 10px 20px;
}

.friend-card {
    letter-spacing: 3px;
    width: 200px;
    height: auto;
    background: white;
    padding: 5px;
    margin: 10px;
    box-shadow: 2px 2px 5px black;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.friend-image {
    width: 100%;
    height: auto;
    box-shadow: 0px 0px 17px black;
}

.details {
    text-align: center;
    font-family: 'Bree Serif', serif;
}
body {
    background: -webkit-linear-gradient(330deg, #08002e, #7f2600);
    background: linear-gradient(120deg, #08002e, #7f2600);
}

.navbar {
    display: flex;
    justify-content: space-between;
    padding: 20px 10px;
