}

.search-input {
    height: 30px;
    margin: 5px;
    border-radius: 3px;
    border: none;
    padding: 5px;
}

.search-icon {
    font-size: 1.2rem;
    color: white;
}

.submit-button {
    background: none;
    border: none;
    padding: 5px;
}
